function Card({ result }) {
  let testResult;
  let type;

  if (result <= 2) {
    testResult = 1;
    type = "success";
  }
  if (result > 2 && result <= 4) {
    testResult = 2;
    type = "success";
  }
  if (result > 4 && result <= 6) {
    testResult = 3;
    type = "warning";
  }
  if (result > 6 && result <= 8) {
    testResult = 4;
    type = "warning";
  }
  if (result > 8 && result <= 10) {
    testResult = 5;
    type = "error";
  }

  const item = results.filter(i => i.id === testResult)[0];

  return (
    <Wrapper>
      <Title level={2}>–†–µ–∑—É–ª—å—Ç–∞—Ç: {item.title}</Title>
      <Alert
        type={type}
        message={`${result} / 10`}
        description={item.text}
        banner
      />
    </Wrapper>
  );
}

export default Card;

